import { styleVariables } from "projectSettings"
import { css } from "styled-components"

export const outlinedRedButton = css`
  color: ${({ theme }) => theme.foreground_tint_red};
  border: 1px solid ${({ theme }) => theme.foreground_tint_red};
  background-color: ${({ theme }) => theme.background_primary};
  :hover {
    color: ${styleVariables.color.black};
    background-color: ${({ theme }) => theme.foreground_tint_red};
  }
`

