import { css } from "styled-components"

const a = css`
  a {
    color: ${({ theme }) => theme.foreground_tint_blue};
    text-decoration: none;
    &:hover {
      color: ${({ theme }) => theme.foreground_tint_blue};
    }
    &:focus-visible {
      outline: none;
    }
  }
`
export default a
